@import '../../constants/variable';

.c_myTransaction
{
    font-size: 14px;
    height: 320px;
    background-color: $bg-right;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    border-radius: 4px;

    @include rwd(ipad)
    {
        width: 100%;
    }

    .title
    {
        @include title($bg-right-title);
    }

    
    .order
    {
        color: $font-gray;
        padding: 10px $div-gap;
        display: flex;
        font-size: 12px;
        
        > div
        {
            cursor: pointer;
            padding: 0 10px 4px 10px;

            &.active
            {
                color: $main-red;
                border-bottom: 2px $main-red solid;
            }

            &:first-child
            {
                margin-right: $div-gap;
            }
        }

        .volume
        {
            color: #fff;
            margin-left: .5em;
        }
    }

    .table
    {
        text-align: center;
        font-size: 12px;

        .t-header
        {
            color: $font-gray;
            display: grid;
            grid-template-columns: repeat(3, 33.33333fr);
            height: 32px;
            line-height: 32px;

            div
            {
                text-align: center;
                word-break: break-all;
                text-align: right;

                &:first-child
                {
                    text-align: left;
                    padding-left: $div-gap;
                }

                &:last-child
                {
                    padding-right: $div-gap;
                }
            }
        }

        .t-body
        {
            overflow-y: auto;
            height: 200px;

            a
            {
                text-decoration: none;
            }

            &::-webkit-scrollbar
            {
                display: none;
            }

            .mt-list
            {
                cursor: pointer;
                padding: 0 $div-gap;
                position: relative;
                display: flex;
                height: 48px;
                align-items: center;

                &.canceled
                {
                    opacity: .4;
                }

                .info
                {
                    display: grid;
                    grid-template-columns: repeat(3, 33.333333fr);
                    color: $font-gray;

                    .desc
                    {
                        color: $font-gray;
                        flex-direction: column;

                        > div
                        {
                            display: flex;

                            > span
                            {
                                &:first-child
                                {
                                    border-radius: 2px;
                                    color: #fff;
                                    display: flex;
                                    width: 16px;
                                    height: 16px;
                                    justify-content: center;
                                    font-size: 12px;
                                    margin-right: 10px;
                                    line-height: 16px;
        
                                    &.buy
                                    {
                                        background-color: $main-green;
                                    }
        
                                    &.sell
                                    {
                                        background-color: $main-red;
                                    }

                                    &.trade
                                    {
                                        background-color: $main-orange;
                                    }
                                }
                                
                                
                                &:last-child
                                {
                                 
                                    flex: 1;
                                    display: flex;
                                    justify-content: flex-start;
                                    
                                    .from
                                    {
                                        font-size: 14px;
                                        color: #fff;
                                    }
                                }
                            }

                            .base
                            {
                                display: flex;
                                align-items: flex-end;
                            }
                        }

                        .time
                        {
                            transform: scale(.833333) translateX(-10%);
                            margin-top: 2px;
                            float: left;
                        }
                    }

                    .price
                    {
                        color: #fff;
                        margin-left: auto;
                    }

                    > div
                    {
                        overflow-x: hidden;
                        justify-content: center;
                        word-break: break-all;

                        &:first-child
                        {
                            justify-content: flex-start;
                            display: flex;
                        }

                        &:nth-child(2)
                        {
                            justify-content: flex-end;
                        }
                    }

                    .type 
                    {
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        > div
                        {
                            width: 10px;
                            height: 10px;
                            border-radius: 50%;
                        }
                        .status
                        {
                            margin-left: 10px;
                        }
                    }


                    .red
                    {
                        color: $main-red !important;
                    }

                    .green
                    {
                        color: $main-green !important;
                    }

                    .item
                    {
                        position: relative;
                        text-align: right;

                        > div
                        {
                            color: #fff;

                            &:nth-child(2)
                            {
                                transform: scale(.833333);
                                float: right;
                                color: $font-gray;
                                margin-top: 2px;
                            }
                        }

                        .full-item
                        {
                            display: none;
                            position: absolute;
                            background-color: #aaa !important;
                            z-index: 100;
                            padding: 4px 8px;
                            top: 50%;
                            left: 50%;
                            transform: translateX(-50%) translateY(-50%);
                            width: 100%;
                            &.red
                            {
                                color: rgb(126, 6, 6);
                            }
                            &.green
                            {
                                color: green;
                            }
                        }

                        &.sell
                        {
                        color: $main-red;
                        }
        
                        &.buy
                        {
                            color: $main-green;
                        }
        
                        &.trade
                        {
                            color: orange;
                        }
        
                        &.cancel
                        {
                            color: $font-gray;
                        }

                        &:hover
                        {
                            .full-item
                            {
                                display: block;
                                pointer-events: none;
                            }
                        }
                    }

                    > .cancel
                    {
                        margin-left: 8px;
                        text-align: right;
                        display: none;
        
                        img
                        {
                            width: 24px;
                            height: 24px;
                            cursor: pointer;
                        }
                    }
                }

                &.block
                {
                    position: relative;
                    pointer-events: none;

                    &::after
                    {
                        content: '';
                        width: 100%;
                        height: 48px;
                        background-color: rgba(15, 15, 15, 0.7);
                        z-index: 10;
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        // pointer-events: none;
                    }

                    &:hover
                    {
                        background-color: transparent;
                        
                        .item.order
                        {
                            padding-left: 0;
                        }

                        .cancel
                        {
                            display: none;
                        }
                    }
                }

                > div
                {
                    font-size: 12px;

                    &:first-child
                    {
                        flex: 1;
                    }
                }

                &::after
                {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 1%;
                    height: 1px;
                    width: 98%;
                    border-bottom: #4a4a4a 1px dotted;
                }

                &:hover
                {
                    background-color:  #070913;
                    
                    .info
                    {
                        > .cancel
                        {
                            display: block;
                        }

                        .price
                        {
                            display: none;
                        }
                    }
                }
            }

            > a
            {
                .mt-list
                {
                    &:hover
                    {
                        .info
                        {
                            .price
                            {
                                display: block;
                            }
                        }
                    }
                }
            }

            .no-result
            {
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: $table-gray;
            }

            .closed .type
            {
                > div
                {
                    background-color: $table-gray;
                }
            }
        }
    }
}
