@import '../../constants/variable';

.co_index
{
    background-color: $bg-black;
    @include container;

    main
    {
        padding: 24px 40px;
        display: flex;
        padding-bottom: 0;
        margin-bottom: 60px;

        @include rwd(ipad)
        {
            padding: 10px;
            // width: 90%;
            flex-direction: column;
            margin-bottom: 130px;
        }

        .c_tokenList
        {
            display: none;

            @include rwd(ipad)
            {
                display: block;
            }
        }

        .sidebar
        {
            width: 32%;
            margin-right: $div-gap;

            > div
            {
                margin-bottom: $div-gap;
            }

            @include rwd(ipad)
            {
               display: none;
               width: 100%;
               margin-right: 0;

               &.rwd-show
               {
                   display: flex;
                   margin-top: 16px;
                   flex-direction: column;
               }
            }
        }

        .content
        {
            width: calc(68% - #{$div-gap});

            .transaction
            {
                display: flex;

                section
                {
                    flex: 1;
                    margin-top: $div-gap;

                    &:first-child
                    {
                        margin-right: $div-gap;
                    }
                }
            }

            @include rwd(ipad)
            {
                width: 100%;

                &.rwd-show
                {
                    >.chart
                    {
                        display: none;
    
                        &.rwd-show
                        {
                            display: flex;
                            margin-top: 16px;
                        }
                    }
    
                    .transaction
                    {
                        display: none;

                        &.rwd-show
                        {
                            display: flex;
                            flex-direction: column;

                            >section:first-child
                            {
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}
