@import '../../constants/variable';

.c_tideWallet
{
    background-image: url('/img/tidewallet.png');
    background-size: cover;
    background-position: center center;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    margin-bottom: 0;
    margin-top: $div-gap;
    padding-top: 24px;
    display: flex;
    justify-content: center;
    border-radius: 4px;
    height: 106px;
  
    @include rwd(ipad)
    {
        display: none;
    }

    > div
    {
        display: flex;
        margin-bottom: 12px;

        .qrcode
        {
            padding: 4px;
            background-color: #fff;
            border-radius: 4px;
        }
    }

    .title
    {
        display: flex;
        flex-direction: column;
        margin-right: 5%;
        align-items: flex-start;

        p
        {   
            font-size: 18px;
            margin-top: 4px;

            span
            {
                font-weight: lighter;
            }

            &:last-child
            {
                font-size: 12px;
                margin-top: 9px;
            }
        }
    }

    .store
    {
        &:last-child
        {
            margin-left: 10px;
        }

        p
        {
            text-align: center;
            padding-top: 5px;
            font-size: 12px;
        }
    }

    p
    {
        margin: 0;
        color: #fff;
    }
}