@import '../../constants/variable';

.c_orders
{
    height: 320px;
    background-color: $bg-right;
    border-radius: 4px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    
    .title
    {
        @include title($bg-right-title);
    }

    .books-header
    {
        width: calc(100% - 50px);
        font-size: 11px;
        display: grid;
        grid-template-columns: 35% 30% 35%;
        color: $font-gray;
        text-align: right;
        height: 32px;
        line-height: 32px;
        margin-left: auto;
        
        > div:last-child
        {
            margin-right: 10px;
        }
    }

    .divid
    {
        border-bottom: 1px dotted #4a4a4a;
        margin: 10px 16px;
    }

    .filter
    {
        display: flex;
        justify-content: space-between;
        color: $font-purple;
        padding: 8px $div-gap;
        font-size: 14px;

        .choose
        {
            position: relative;
            display: flex;
            align-items: center;
            cursor: pointer;    

            span
            {
                margin-right: $div-gap;
            }

            .toggle
            {
                @include toggle(4);
                text-align: right;

                > div
                {
                    padding-right: 24px;
                }
            }
        }

        .more
        {
            a 
            {
                text-decoration: none;
                color: $font-purple;
            }
        }
    }
}