@import '../../constants/variable';

div.c_content
{
    width: 100%;
    background-color: $bg-right;
    border-radius: 4px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    height: 464px;

    @include rwd(ipad)
    {
        height: auto;
    }

    > div
    {
        display: flex;
    }

    .title
    {
        justify-content: space-between;
        background-color: $bg-right-title;
        padding: 0 $div-gap;
        color: #fff;
        height: 48px;
        font-size: 12px;

        .control
        {
            padding: 0 24px;
            display: flex;
            color: $font-gray;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;

            > div
            {
                display: flex;
            }

            .switch
            {
                padding: 0 18px;
                display: flex;
                cursor: pointer;
                padding-bottom: 8px;
                position: relative;
    
                &.active
                {
                    color: #fff;
                    border-bottom: 2px #fff solid;
                    padding-bottom: 6px;
                }

                .c_prompt
                {
                    margin-left: 8px;
                }

            }
        }

    }

    .dashboard
    {
        display: flex;
        flex-direction: column;
        // height: 408px;
        overflow: hidden;
        transition: .4s;

        @include rwd(ipad)
        {
           height: auto;
        }

        &.close
        {
            height: 0;
        }

        .info
        {
            font-size: 12px;
            margin-right: 30px;
            text-align: right;

            &:last-child
            {
                margin-right: 0;
            }

            > div:last-child
            {
                font-size: 14px;
                color: #fff;
                margin-top: 4px;
            }

            @include rwd(ipad)
            {
               text-align: left;
               padding-top: 10px;
            }
            
        }

        .react-stockchart
        {
            // width: 100% !important;
            // height: 100% !important;
        }

        .unit
        {
            margin-left: auto;
            color: $font-gray;
            padding: $div-gap 24px;
        }

        .chart
        {
            min-height: 360px;
            padding: 0 24px;

            @include rwd(ipad)
            {
                padding: 0 10px;
            }
        }

        .no-result
        {
            color: $font-gray;
        }
    }

    .select
    {
        position: relative;
        cursor: pointer;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 14px;
        color: $font-purple;

        span
        {
            margin-right: 10px;
        }

        img
        {
            width: 10px;
        }

        .toggle
        {
            @include toggle(4);
            min-width: 60px;
        }
    }

    
}
