@import '../../constants/variable';

.c_account
{
    color: $font-gray;
    background-color: #000;
    border-radius: 4px;
    margin-bottom: $div-gap;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    height: 464px;
    display: flex;
    flex-direction: column;

    .title
    {
        @include title($bg-right-title);
        display: flex;
        justify-content: space-between;

        .logout
        {
            color: $font-purple;
            cursor: pointer;
        }
    }

    .table
    {
        display: flex;
        padding: $div-gap;
        flex-direction: column;
        border-bottom: none;
        position: relative;

        &::after
        {
            content: '';
            width: 95%;
            border-bottom: 1px #4a4a4a dotted;
            bottom: 0;
            position: absolute;
            left: 2.5%;
        }

        .logout
        {
            padding-bottom: $div-gap;
            border-bottom: dotted 1px #4a4a4a;
        }

        .button
        {
            @include button;
            display: block;
            float: right;
        }

        .t-header
        {
            text-align: right;
            padding-top: $div-gap;

            span 
            {
                color: $font-gray;
                float: left;
                font-size: .67em;
            }
        }

        .t-body
        {
            width: 100%;
            height: 56px;
            overflow: auto;
            
            &::-webkit-scrollbar
            {
               background-color: transparent;
               width: 5px;
            }

            &::-webkit-scrollbar-thumb
            {
                border-radius: 10px;
                background-color: #4a4a4a;
            }

            > div
            {
                display: grid;
                grid-template-columns: 20% 50% 30%;
                margin-top: $div-gap;
                color: #fff;
                text-align: left;
                font-size: 12px;

                .add
                {
                    font-weight: bold;
                    color: $main-green;
                }
                .less
                {
                    font-weight: bold;
                    color: $main-red;
                }
            }
        }
    }

    .wallet-address
    {
        color: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: $div-gap;

        .wallet-title
        {
            color: $font-gray;
            font-size: 12px;
            margin-bottom: 5px;
        }

        .address
        {
            flex: 1;
        }

        textarea
        {
            background-color: transparent;
            border: none;
            color: #fff;
            word-break: break-all;
            word-wrap: break-word;
            outline: none;
            resize: none;
            font-size: 12px;
            height: 12px;
            width: 68%;
        }

        img
        {
            cursor: pointer;
        }
    }

    .wallet-export
    {
        // padding-bottom: $div-gap;

        a
        {
            text-decoration: none;
        }
    }

    > div.buySell
    {
        display: flex;
        flex-direction: column;
        padding: $div-gap;
        padding-top: 0;
        font-size: 14px;
        color: $font-gray;
        flex: 1;

        > div 
        {
            display: flex;

            &:last-child
            {
                flex: 1;
            }
        }
    
        .pending-type
        {
            margin-top: $div-gap;
            
            a
            {
                padding-bottom: 4px;
                margin-right: 24px;
                cursor: pointer;
                font-size: 12px;
            }
            
           .active
           {
               color: #fff;
               border-bottom: 2px solid #fff;
               border-radius: 0;
           }
        }

        .buy-sell
        {
            color: $font-purple;
            font-size: 12px;

            .select
            {
                margin: 10px 0;
                display: flex;
                align-items: center;
                position: relative;
                cursor: pointer;

                span
                {
                    margin-right: $div-gap;
                    z-index: 1;
                }
            }

            .toggle
            {   
                @include toggle(2);
            }


            .store
            {
                margin-left: auto;
                display: flex;
                align-items: center;
                color: #fff;

                img 
                {
                    margin-right: 5px;
                }

                span
                {
                    display: flex;
                    align-items: center;

                    > span
                    {
                        margin-right: 5px;
                    }

                    div
                    {
                        width: 4px;
                        height: 1px;
                        position: relative;
                        margin-right: 2px;
                        
                        &::before
                        {
                            position: absolute;
                            content: '';
                            width: 100%;
                            height: 1px;
                            border-bottom: #fff solid 1px;
                        }
                    }
                }
            }
        }

        .address
        {
            display: flex;
            flex-direction: column;
            align-items: center;
            > div
            {
                display: flex;
                flex-direction: column;
                align-items: center;
                p
                {
                    font-size: 10px;
                    margin-bottom: 16px;
                    color: $font-gray;
                }
                &.hide
                {
                    opacity: 0;
                    height: 0;
                    transition: .4s;
                }
                &.show
                {
                    opacity: 1;
                    height: 250px;
                    transition: .4s;
                }
                .download
                {
                    text-decoration: none;
                    padding: 3px;
                }
                .logout
                {
                    padding: 4px 8px;
                    &:hover
                    {
                        background-color: $main-gray;
                        color: #fff;
                    }
                }
                > div
                {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
            }
            > p
            {
                padding: 6px 30px;
                margin: 0;
                color: #fff;
                cursor: pointer;
                border-radius: 6px;
                &:hover
                {
                    background: linear-gradient(to bottom, rgba(255, 255, 255, .5), rgba(255, 255, 255, .3));
                    transition: .5s;
                }
            }
        }
    }

    .loginCreate
    {
        text-align: center;
        // opacity: .9;
        padding: $div-gap;
        &.none
        {
            display: none;
        }

        @include rwd(ipad)
        {
            width: 100%;
        }
    }

    
    .full::before
    {
        position: fixed;
        content: '';
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, .7);
        transition: .5s;
        z-index: 999;
    }

    .qrcode
    {
        padding: 10px;
        background-color: #fff;
    }

    .cover
    {
        background-color: rgba(0, 0, 0, .5);
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 9999;
    }

    .pending-modal
    {
        width: 50%;
        position: absolute;
        top: 25%;
        left: 25%;
        border-radius: 20px;
        background-color: #dddd;
        color: $main-gray;
        h2
        {
            text-align: center;
        }
        > div
        {
            position: relative;
        }
        a
        {
            position: absolute;
            top: -5px;
            right: 15px;
            cursor: pointer;
        }
        > div > div
        {
            display: flex;
            flex-direction: column;
            align-items: center;
            p
            {
                text-align: center;
                margin: 10px;
            }
            button
            {
                margin-bottom: 20px;
                width: 60%;
                padding: 10px 0;
                border-radius: 20px;
                cursor: pointer;
                color: #fff;
                transition: .5s;
                &.red
                {
                    background-color: rgb(75, 3, 15);
                    &:hover
                    {
                        background-color: rgb(156, 9, 33);
                    }
                }
                &.green
                {
                    background-color: rgb(9, 73, 57);
                    &:hover
                    {
                        background-color: rgb(9, 109, 84);
                    }
                }
                &:disabled
                {
                    background-color: $font-gray;
                    cursor: not-allowed;
                    &:hover
                    {
                        background-color: $font-gray;
                    }
                }
            }
        }
        .warning
        {
            text-align: center;
            padding: 10px 0;
            border-radius: 0 0 20px 20px;
            background: $main-orange;
            color: #fff;
            opacity: .5;
        }
        
    }

    .input-field
    {
        display: flex;
        margin-bottom: 10px;
        justify-content: space-between;
        align-items: center;
        font-size: 13px;

        > div
        {
            &:first-child
            {
                width: 15%;
            }

            &:last-child
            {
                border: 1px solid $font-gray;
                width: 85%;
                height: 18px;
                padding: 6px;

                &.active
                {
                    border: #fff 1px solid;
                }
            }
        }
    }

    .enter
    {
        display: flex;
        cursor: pointer;
        
        input
        {
            width: 50%;
            background-color: transparent;
            border: none;
            box-shadow: 0;
            outline: none;
            color: $font-purple;
            line-height: 18px;
            -webkit-text-fill-color: $font-gray;
            letter-spacing: .1em;
            cursor: pointer;

            &:focus
            {
                -webkit-text-fill-color: #fff;
            }
        }

        .type
        {
            width: 50%;
            text-align: center;
            line-height: 18px;
        }
    }

    .transfer
    {
        display: flex;
        justify-content: center;
        margin: 10px 0;
    }

    .price
    {
        display: flex;
        justify-content: space-between;
        > div span
        {
            margin-right: 10px;
        }
        .sell-price span
        {
            color: $main-red;
        }
        .buy-price span
        {
            color: $main-green;
        }
    }
    
    .from, .to
    {
        width: 40px;
        text-align: right;
    }

    .volume
    {
        border: none;
        display: flex;
        flex-direction: column;

        > div:first-child
        {
            margin-top: 24px;
            margin-bottom: 18px;
        }

        > div:last-child
        {
            display: flex;
            color: #fff;

            .total
            {
                text-align: right;
                color: #fff;
                width: 100%;
                background-color: transparent;
                border: none;
                margin-right: 8px;
                letter-spacing: .1em;
            }
        }
    }

    .submit
    {
        font-size: 12px;
        transition: .4s;
        display: flex;
        align-items: flex-end;

        button
        {
            width: 100%;
            height: 2em;
            line-height: 2em;
            padding: 0;
            color: #fff;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            outline: none;
            &:disabled
            {
                cursor: not-allowed;
            }

            &.green
            {
                background-color: $main-green;
            }

            &.red
            {
                background-color: $main-red;
            }
        }
    }

    .submit-field
    {
        display: flex;
        margin-top: auto;

        > div
        {
            flex: 1;
            &:first-child
            {
                margin-right: 5%;
            }
        }
    }
}

