.c_mortgageLink
{
    background-image: url('/img/mortgage.png');
    background-size: cover;
    padding: 17px 24px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    border-radius: 4px;
    height: 93px;
    padding-bottom: 20px;
    background-position: bottom right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    p
    {
        margin: 0;
    }

    h3
    {
        color: #fff;
        margin: 0;
        font-weight: normal;
        font-size: 18px;
        margin-bottom: 30px;

        p
        {
            margin-bottom: 4px;
        }
    }

    a
    {
        text-decoration: none;
        color: #000;
        display: flex;
        justify-content: flex-end;

        i
        {
            margin-left: 16px;
        }
    }
}