@import '../../constants/variable';

.no-result
{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

svg.c_deepChart
{
    width: 100%;
    height: 400px;

    cursor: crosshair;

    .sell-line
    {
        fill: none;
        stroke: red;
        stroke-width: 2px;
    }

    .sell-area
    {
        fill: rgba(255, 85, 85, .3);
    }

    .buy-line
    {
        fill: none;
        stroke: green;
        stroke-width: 2px;
    }

    .middle-line
    {
        stroke: rgba(226, 152, 14, .3);
        stroke-width: 2px;
        &:hover
        {
            stroke: red;
        }
    }

    .buy-area
    {
        fill: rgba(0, 221, 170, .3);
    }

    .grid
    {
        line
        {
            stroke: rgba(255, 255, 255, .5);
        }
    }

    .circle
    {
        fill: $main-orange;
    }

    .point-line
    {
        stroke: rgba(255, 255, 255, .3);
        stroke-width: 2px;
    }

    .tick
    {
        stroke: rgba(255, 255, 255, .3);
        text
        {
            font-size: 12px;
        }
    }

    .show
    {
        opacity: 1;
    }

    .hide
    {
        opacity: 0;
    }

    tspan
    {
        font-size: 12px;
    }

    .red
    {
        stroke: $main-red;
    }

    .green
    {
        stroke: $main-green;
    }

    .point
    {
        fill-opacity: 0;
    }
}
