@import '../../constants/variable';

.c_recentTransaction
{
    font-size: 13px;
    text-align: center;
    background-color: $bg-right;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    border-radius: 4px;
    height: 320px;

    .title
    {
        @include title($bg-right-title);
        text-align: left;
        display: flex;
        justify-content: space-between;

        img
        {
            cursor: pointer;
        }
    }

    > table
    {
        width: 100%;
        border-collapse: collapse;
        color: white;

         > thead > tr
        {
            margin: 1px 0;
        }

        tbody
        {
            display: block;
        }
    }

    tr
    {
        display: flex;

        th
        {
            line-height: 2.5;

            flex: 1;

            color: $font-gray;
        }

        td
        {
            flex: 1;

            padding: 5px 0;

            &.time
            {
                color: $font-gray;
            }

            &.up
            {
                color: $main-red;
            }

            &.down
            {
                color: $main-green;
            }
        }
    }

    .red
    {
        color: $main-red !important;
    }

    .green
    {
        color: $main-green !important;
    }
    .table
    {
        color: #ffffff;
        height: 280px;
        transition: .4s;
        overflow: hidden;
        font-size: 12px;

        &.close
        {
            height: 0;
        }

        .t-header
        {
            color: $font-gray;
            display: grid;
            grid-template-columns: 50px repeat(3, 33.3333fr);
            height: 28px;
            line-height: 28px;
            text-align: right;

            span:last-child
            {
                padding-right: $div-gap;
            }
        }

        .t-body
        {
            overflow-y: auto;
            height: 260px;
            font-size: 12px;

            &::-webkit-scrollbar
            {
                display: none;
            }

            .info
            {
                
                height: 24px;
                display: grid;
                grid-template-columns: 50px repeat(3, 33.3333fr);
                padding: 4px 0;
                color: #fff;
                text-align: right;

                &:first-child
                {
                    padding-top: 0;
                }

                > div:first-child
                {
                    text-align: left;
                    padding-left: 8px;
                }

                > div:last-child
                {
                    padding-right: $div-gap;
                }
            }

            .time
            {
                color: $font-gray;
                text-align: right;
                padding-right: $div-gap;
            }
        }
    }
    
}
